.swiper {
  display: none;
  width: 100%;
  height: 100%;

  @include onMobile {
    display: block;
  }
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  min-height: 600px;
  padding: 30px 0;

  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}
